<template>
  <div class="no-select p-2">

    <div class="row mx-0 align-items-center">

      <div class="col-sm-4">
        <input type="file" accept="image/jpeg,image/png,image/gif" v-on:change="onImageFileSelected" class="d-none"
               ref="fileInputElement"/>

        <div class="aspect-100" id="store-image">
          <img src="../../../assets/images/brand-icon.jpeg" ref="storeImageElement"/>

          <div id="store-image-upload-progress"></div>
        </div>

        <div class="my-3 text-center">
          <span class="icon-buttons m-1 text-warning p-1 cursor-pointer" title="انتخاب تصویر"
                v-if="!cropper && progressPercentage === 0"
                @click="openFile">
            <span class="fa fa-folder-open fa-2x"></span>
          </span>


          <span class="icon-buttons m-1 text-primary p-1 cursor-pointer" title="بارگذاری تصویر"
                v-if="cropper && progressPercentage === 0"
                @click="cropImageAndUpload">
            <span class="fa fa-cloud-upload-alt fa-2x"></span>
          </span>

          <span class="icon-buttons m-1 text-success p-1 cursor-pointer" title="بارگذاری تصویر"
                v-if="progressPercentage > 0">
            <span class="fa fa-spin fa-spinner fa-2x"></span>
          </span>
        </div>
      </div>

      <div class="col-sm-8">

        <div class="form-row">

          <div class="form-group col-md">
            <label>عنوان برند</label>
            <input type="text" class="form-control" value="گیلانی کار"/>
          </div>

          <div class="form-group col-md">
            <label>نوع خدمات</label>
            <select class="form-control">
              <option>فروشگاه فیزیکی</option>
              <option>فروشگاه اینترنتی</option>
              <option selected>عرضه کننده خدمات اینترنتی</option>
              <option>دیگر</option>
            </select>
          </div>

        </div>

        <div class="form-row">

          <div class="form-group col-md">
            <label>شماره تماس</label>
            <input type="tel" class="form-control" value="09365703014"/>
          </div>

          <div class="form-group col-md">
            <label>ایمیل</label>
            <input type="email" class="form-control text-right" dir="ltr" value="info@gilanikar.ir"/>
          </div>

        </div>

        <div class="form-group">
          <label>وبسایت</label>
          <input type="url" class="form-control text-right" dir="ltr" value="https://gilanikar.ir"/>
        </div>

        <div class="form-group">
          <label>درباره برند</label>
          <textarea class="form-control">
            وبسایت ارائه دهنده خدمات دیجیتال (درحال راه اندازی)
          </textarea>
        </div>

        <div class="form-group">
          <label>آدرس و راه‌های ارتباط دیگر</label>
          <textarea class="form-control">

          </textarea>
        </div>

        <div class="form-group">
          <div class="input-group">
            <input type="password" placeholder="کلمه عبور" class="form-control">
            <div class="input-group-append">
              <button class="btn btn-success">
                بروزرسانی
              </button>
            </div>
          </div>
          <div class="small text-muted">
            جهت بروزرسانی اطلاعات فروشگاه وارد کردن کلمه عبور الزامی است
          </div>
        </div>

      </div>

    </div>

  </div>
</template>

<script>
import Cropper from 'cropperjs';
import 'cropperjs/dist/cropper.min.css'

export default {
  name: "StoreManagement", data: () => ({
    cropper: null,
    progressPercentage: 0
  }),
  methods: {
    openFile: function () {
      this.$refs.fileInputElement.click();
    },
    onImageFileSelected: function () {

      const files = this.$refs.fileInputElement.files;
      if (files.length > 0 && files[0]) {
        const fd = new FileReader();
        fd.onload = () => {
          this.$refs.storeImageElement.src = fd.result;

          this.cropper = new Cropper(this.$refs.storeImageElement, {
            aspectRatio: 1 / 1
          })
        }

        fd.readAsDataURL(files[0]);
      }
    },
    cropImageAndUpload: function () {
      if (this.cropper) {
        const jobDone = () => {
          const src = this.cropper.getCroppedCanvas().toDataURL();
          this.cropper.destroy();
          this.$refs.storeImageElement.src = src;
          this.cropper = null;
        }
        const interval = setInterval(() => {
          const progress = document.getElementById('store-image-upload-progress');
          if (this.progressPercentage < 100) {
            this.progressPercentage += 1;
            progress.style.top = 100 - (this.progressPercentage) + '%';
          } else {
            this.progressPercentage = 0;
            progress.style.top = '100%';
            clearInterval(interval);
            jobDone();
          }
        }, 100)
      }
    }
  }
}
</script>

<style scoped>
#store-image {
  border-radius: 5px;
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.8);
}

#store-image-upload-progress {
  background: rgba(0, 255, 0, 0.6);
  top: 100%;
}
</style>